.leagues {
  padding-left: 10px;
  font-family: HelveticaNeue;
}
.leagues .text-left {
  text-align: left;
}
.leagues .filter .col-sm-1.col-md-1.col-lg-1.tournament-type-checkbox {
  width: 10%;
}
.leagues .filter .tournament-type-checkbox {
  padding-top: 6px;
}
.leagues .filter .filter-content .col-sm-1,
.leagues .filter .filter-content .col-md-1,
.leagues .filter .filter-content .col-lg-1 {
  width: 15%;
}
.leagues hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: solid 1px #e9e9e9;
  padding: 30px 0 0 0;
  margin-right: -8px;
}
.leagues .btn-status {
  width: 22%;
  margin-left: 0;
  margin-right: 7px;
}
.leagues .search-table-container h2 {
  font-family: HelveticaNeue;
  font-size: 18px;
  font-weight: bold;
  text-align: left;
  color: #000000;
}
.leagues .search-table-container .table-options {
  font-family: HelveticaNeue;
  font-size: 14px;
  text-align: left;
  color: #444444;
}
.leagues .search-table-container .table-options .control {
  padding-left: 0;
}
.leagues .search-table-container .table-options .control input {
  width: 65px;
  height: 26px;
  border-radius: 2px;
  border: solid 1px rgba(150, 150, 150, 0.31);
  font-size: 14px;
}
.leagues .search-table-container .table-options .control label {
  font-size: 14px !important;
}
.leagues .search-table-container .table-options .control label.total-page {
  margin-left: -40px;
}
.leagues .search-table-container .table-options .control #record-per-page {
  margin-left: -10px;
  margin-top: -4px;
}
.leagues .search-table-container .table-options .control #go-to {
  margin-left: -45px;
  margin-top: -4px;
}
.leagues .search-table-container .table-options .control .page-option .col-sm-2,
.leagues .search-table-container .table-options .control .page-option .col-md-2,
.leagues .search-table-container .table-options .control .page-option .col-lg-2 {
  width: 15%;
}
.leagues .search-table-container .table-options label {
  font-weight: normal;
}
.leagues .search-table-container .table-container {
  padding: 30px 0 30px 0;
}
.leagues .search-table-container .table-container .table-header-wrapper {
  padding: 0;
  border: solid 1px #ebebeb;
  border-bottom: none;
  border-bottom: 5px solid #f7f7f7;
}
.leagues .search-table-container .table-container .table-header-wrapper .table-header {
  padding: 20px 0;
  font-family: HelveticaNeue;
  font-size: 14px;
  text-align: center;
  color: #4a4a4a;
  font-weight: bold;
}
.leagues .search-table-container .table-container .table-header-wrapper .border-right {
  border-right: solid 1px #ebebeb;
}
.leagues .search-table-container .table-container .table .text-fuzzy {
  color: #9d9d9d;
}
.leagues .search-table-container .table-container .table td {
  font-size: 14px;
  border: solid 1px #ebebeb;
  text-align: center;
  color: #4a4a4a;
  padding: 10px 0 10px 0;
}
.leagues .search-table-container .table-container .table .competition-name {
  padding: 60px 0 0;
  font-weight: bold;
  background-color: #fff;
}
.leagues .search-table-container .table-container .table .classification {
  background-color: #fff;
  padding: 25px 0 0;
}
.leagues .search-table-container .table-container .table .action a {
  color: #2381da;
  font-weight: bold;
  float: right;
  padding-right: 20px;
}
.leagues .search-table-container .table-container .table .btn-pencil {
  position: relative;
  background-color: #fff;
  border: none;
  color: white;
}
.leagues .search-table-container .table-container .table .btn-pencil:before {
  content: "\f040";
  font-family: FontAwesome;
  font-style: normal;
  font-weight: normal;
  text-decoration: inherit;
  color: #2381da;
  font-size: 18px;
  position: absolute;
  top: -15px;
  left: -30px;
}
.leagues .search-table-container .table-container #table-scroll {
  max-height: 170px;
}
.leagues .search-table-container .table-container #table-scroll .mCSB_container {
  margin-right: 0px;
}
.leagues .search-table-container .table-container #table-scroll .mCSB_draggerContainer {
  left: 12px;
}
.leagues .search-table-container .table-container #table-scroll .mCSB_dragger_bar {
  width: 4px;
  border-radius: 4.5px;
  background-image: linear-gradient(to bottom, #2379ae, #53a0fe);
  box-shadow: 0 0 4px 0 #4990e2;
}
.leagues .search-table-container .custom-pagination a {
  font-family: HelveticaNeue;
  font-size: 14px;
  font-weight: bold;
  text-align: left;
  color: #444444;
  padding: 0 5px 0 5px;
}
.leagues .search-table-container .custom-pagination .selected {
  background: #8dc63f;
  width: 18px;
  height: 18px;
  display: inline-block;
  border-radius: 2px;
  padding: 0px 0 0 5px;
  color: #fff;
}
