.organization {
  font-family: HelveticaNeue;
  margin-left: -20px;
}
.organization .col-md-2.control-label {
  width: 19%;
}
.organization .big-label label {
  font-size: 20px !important;
}
.organization .custom-file-upload label {
  font-weight: normal;
}
.organization .btn-remove {
  border-radius: 2.7px;
  background-color: #dd8c8c;
  border: solid 0.9px #dd8c8c;
  color: #fff;
  padding: 5px 25px;
  cursor: pointer;
  text-align: center;
}
.organization label.text-left {
  text-align: left;
  margin-left: 70px;
}
.organization .text {
  font-family: HelveticaNeue;
  font-size: 18px;
  text-align: left;
  color: #000000;
}
.organization .sub-organization label.text-left {
  margin-left: 40px;
}
.organization .sub-organization .table-container {
  padding: 30px 0 30px 55px;
}
.organization .sub-organization .table-container .table-header-wrapper {
  padding: 0;
  border: solid 1px #ebebeb;
  border-bottom: 5px solid #f7f7f7;
}
.organization .sub-organization .table-container .table-header-wrapper .table-header {
  padding: 20px 0;
  font-family: HelveticaNeue;
  font-size: 14px;
  text-align: center;
  color: #4a4a4a;
  font-weight: bold;
  position: relative;
}
.organization .sub-organization .table-container .table-header-wrapper .sort-able:before {
  content: "";
  width: 0;
  height: 0;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
  position: absolute;
  top: 0;
  border-top: 7px solid #000;
  text-align: center;
  right: 13%;
  top: 25px;
}
.organization .sub-organization .table-container .table-header-wrapper .border-right {
  border-right: solid 1px #ebebeb;
}
.organization .sub-organization .table-container .table .text-fuzzy {
  color: #9d9d9d;
}
.organization .sub-organization .table-container .table td {
  font-size: 14px;
  border: solid 1px #ebebeb;
  text-align: center;
  color: #4a4a4a;
  padding: 10px 0 10px 0;
}
.organization .sub-organization .table-container .table .sub-organization-name {
  font-weight: bold;
}
.organization .sub-organization .table-container .table .classification {
  background-color: #fff;
  padding: 25px 0 0;
}
.organization .sub-organization .table-container .table .action a {
  color: #dd8c8c;
  font-weight: bold;
  float: right;
  padding-right: 40px;
  position: relative;
}
.organization .sub-organization .table-container .table .action a:before {
  content: "";
  background-image: url("../src/image/organization-icon/icon-remove.png");
  background-repeat: no-repeat;
  background-size: 13px 16px;
  width: 17px;
  height: 20px;
  display: inline-block;
  text-align: left;
  margin: 0 20px 0 0;
  position: absolute;
  top: 4px;
  right: 65px;
}
.organization .sub-organization .table-container .table .action .btn-add-sub-organization {
  color: #8dc63f;
  font-weight: bold;
  cursor: pointer;
}
.organization .sub-organization .table-container .table .logo a {
  color: #2381da;
}
.organization .sub-organization .table-container .table .btn-pencil {
  position: relative;
  background-color: #fff;
  border: none;
  color: white;
}
.organization .sub-organization .table-container .table .btn-pencil:before {
  content: "";
  background-image: url("../src/image/logo-2x/icon-edit2x.png");
  background-repeat: no-repeat;
  background-size: 15px 15px;
  width: 20px;
  height: 20px;
  display: inline-block;
  text-align: left;
  margin: 0 20px 0 0;
  position: absolute;
  top: -11px;
  left: -30px;
}
.organization .sub-organization .table-container #table-scroll {
  max-height: 170px;
}
.organization .sub-organization .table-container #table-scroll .mCSB_container {
  margin-right: 0px;
}
.organization .sub-organization .table-container #table-scroll .mCSB_draggerContainer {
  left: 12px;
}
.organization .sub-organization .table-container #table-scroll .mCSB_dragger_bar {
  width: 4px;
  border-radius: 4.5px;
  background-image: linear-gradient(to bottom, #2379ae, #53a0fe);
  box-shadow: 0 0 4px 0 #4990e2;
}
#addSubOrganizationModal {
  font-size: 16px;
}
#addSubOrganizationModal input {
  height: 43px;
}
#addSubOrganizationModal .avatar-setup {
  float: left;
  width: 32%;
}
#addSubOrganizationModal .avatar-setup img {
  border: 1px solid #dddddd;
  padding: 3px;
  border-radius: 4px;
}
#addSubOrganizationModal .btn-remove {
  font-size: 14px;
  border-radius: 2.7px;
  background-color: #dd8c8c;
  border: solid 0.9px #dd8c8c;
  color: #fff;
  padding: 5px 25px;
  cursor: pointer;
  text-align: center;
}
#addSubOrganizationModal .label-file {
  font-weight: normal;
}
#addSubOrganizationModal .big-label label {
  font-size: 20px !important;
}
#addSubOrganizationModal .custom-file-upload {
  float: left;
}
#addSubOrganizationModal .custom-file-upload .inputfile {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}
#addSubOrganizationModal .custom-file-upload .inputfile + label {
  border-radius: 2.7px;
  background-color: #8dc63f;
  border: solid 0.9px #8dc63f;
  color: #fff;
  padding: 5px 25px;
  cursor: pointer;
  text-align: center;
  font-size: 14px;
}
#addSubOrganizationModal .custom-file-upload .inputfile:focus + label,
#addSubOrganizationModal .custom-file-upload .label-file {
  width: 100%;
}
.organization-start .img-info {
  width: 139px ;
  height: 139px;
  margin-bottom: 20px;
}
.organization-start h3 {
  font-family: Archive;
  font-size: 26px;
  text-align: center;
  color: #000000;
  font-weight: normal;
  margin-top: 20px !important;
  margin-bottom: 20px !important;
}
.organization-start button {
  font-size: 20px;
  width: 278px;
  height: 58px;
  margin-top: 20px !important;
}
