.tournament-list {
  /*margin-top: -60px;*/
  padding-left: 20px;
  font-family: HelveticaNeue;
}
.tournament-list .text-left {
  text-align: left;
}
.tournament-list .filter #year {
  width: 80px;
}
.tournament-list .filter .filter-content .year-filter {
  width: 6em !important;
}
.tournament-list .filter .filter-content .status-filter {
  width: 10em !important;
}
.tournament-list .filter .filter-content .classification-filter {
  width: 13em !important;
}
.tournament-list .filter .col-sm-1.col-md-1.col-lg-1.tournament-type-checkbox {
  width: 12%;
}
.tournament-list .filter .tournament-type-checkbox {
  padding-top: 6px;
}
.tournament-list .filter .filter-footer {
  padding-right: 20px;
  margin-top: -20px;
}
.tournament-list .filter .calendar-icon {
  border: 0px;
  background: #fff;
  position: absolute;
  top: 8px;
  right: 30px;
  z-index: 2;
}
.tournament-list hr {
  display: block;
  height: 1px;
  border: 0;
  border-top: solid 1px #e9e9e9;
  padding: 30px 0 0 0;
  margin-right: -8px;
}
.tournament-list .search-table-container h2 {
  font-family: HelveticaNeue;
  font-size: 18px;
  font-weight: bold;
  text-align: left;
  color: #000000;
}
.tournament-list .search-table-container .table-options {
  font-family: HelveticaNeue;
  font-size: 14px;
  text-align: left;
  color: #444444;
}
.tournament-list .search-table-container .table-options .control {
  padding-left: 0;
}
.tournament-list .search-table-container .table-options .control input {
  width: 65px;
  height: 26px;
  border-radius: 2px;
  border: solid 1px rgba(150, 150, 150, 0.31);
  font-size: 14px;
}
.tournament-list .search-table-container .table-options .control label {
  font-size: 14px !important;
}
.tournament-list .search-table-container .table-options .control label.total-page {
  margin-left: -40px;
}
.tournament-list .search-table-container .table-options .control #record-per-page {
  margin-left: -10px;
  margin-top: -4px;
}
.tournament-list .search-table-container .table-options .control #go-to {
  margin-left: -45px;
  margin-top: -4px;
}
.tournament-list .search-table-container .table-options .control .page-option .col-sm-2,
.tournament-list .search-table-container .table-options .control .page-option .col-md-2,
.tournament-list .search-table-container .table-options .control .page-option .col-lg-2 {
  width: 14%;
}
.tournament-list .search-table-container .table-options label {
  font-weight: normal;
}
.tournament-list .search-table-container .table-options input[type=number]::-webkit-inner-spin-button,
.tournament-list .search-table-container .table-options input[type=number]::-webkit-outer-spin-button {
  opacity: 1;
  margin-right: -1px;
  background: #fff;
}
.tournament-list .search-table-container .table-container {
  padding: 30px 0 30px 0;
}
.tournament-list .search-table-container .table-container .table-header-wrapper {
  padding: 0;
  border: solid 1px #ebebeb;
  border-bottom: 5px solid #f7f7f7;
}
.tournament-list .search-table-container .table-container .table-header-wrapper .table-header {
  padding: 20px 0;
  font-family: HelveticaNeue;
  font-size: 14px;
  text-align: center;
  color: #4a4a4a;
  font-weight: bold;
  position: relative;
}
.tournament-list .search-table-container .table-container .table-header-wrapper .sort-able:before {
  content: "";
  width: 0;
  height: 0;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
  position: absolute;
  top: 0;
  border-top: 7px solid #000;
  text-align: center;
  right: 13%;
  top: 25px;
}
.tournament-list .search-table-container .table-container .table-header-wrapper .border-right {
  border-right: solid 1px #ebebeb;
}
.tournament-list .search-table-container .table-container .table .text-fuzzy {
  color: #9d9d9d;
}
.tournament-list .search-table-container .table-container .table td {
  font-size: 14px;
  border: solid 1px #ebebeb;
  text-align: center;
  color: #4a4a4a;
  padding: 10px 0 10px 0;
}
.tournament-list .search-table-container .table-container .table .competition-name {
  font-weight: bold;
}
.tournament-list .search-table-container .table-container .table .classification {
  background-color: #fff;
  padding: 25px 0 0;
}
.tournament-list .search-table-container .table-container .table .action a {
  color: #2381da;
  font-weight: bold;
  float: right;
  padding-right: 20px;
}
.tournament-list .search-table-container .table-container .table .btn-pencil {
  position: relative;
  background-color: #fff;
  border: none;
  color: white;
}
.tournament-list .search-table-container .table-container .table .btn-pencil:before {
  content: "";
  background-image: url("../src/image/logo-2x/icon-edit2x.png");
  background-repeat: no-repeat;
  background-size: 15px 15px;
  width: 20px;
  height: 20px;
  display: inline-block;
  text-align: left;
  margin: 0 20px 0 0;
  position: absolute;
  top: -11px;
  left: -30px;
}
.tournament-list .search-table-container .table-container #table-scroll {
  max-height: 170px;
}
.tournament-list .search-table-container .table-container #table-scroll .mCSB_container {
  margin-right: 0px;
}
.tournament-list .search-table-container .table-container #table-scroll .mCSB_draggerContainer {
  left: 12px;
}
.tournament-list .search-table-container .table-container #table-scroll .mCSB_dragger_bar {
  width: 4px;
  border-radius: 4.5px;
  background-image: linear-gradient(to bottom, #2379ae, #53a0fe);
  box-shadow: 0 0 4px 0 #4990e2;
}
.tournament-list .search-table-container .custom-pagination a {
  font-family: HelveticaNeue;
  font-size: 14px;
  font-weight: bold;
  text-align: left;
  color: #444444;
  padding: 0 5px 0 5px;
}
.tournament-list .search-table-container .custom-pagination .selected {
  background: #8dc63f;
  width: 18px;
  height: 18px;
  display: inline-block;
  border-radius: 2px;
  padding: 0px 0 0 5px;
  color: #fff;
}
