.competition-category-chart {
  padding-left: 49px;
}
.competition-category-chart .category-chart-node {
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  color: #fff;
  position: relative;
}
.competition-category-chart .category-chart-node:hover .btn-close {
  display: block;
}
.competition-category-chart .court-size-node {
  width: 200px;
  position: relative;
}
.competition-category-chart .gender-node {
  /*padding: 10px 0 10px 0px;*/
  background-color: #8dc63f;
  width: 100px;
  height: 40px;
  line-height: 38px;
  font-size: 14px;
  font-weight: bold;
  text-align: center;
  color: #fff;
  position: relative;
}
.competition-category-chart .new-category-button-container {
  width: 140px;
  height: 80px;
  border: solid 1px #8dc63f;
  background: #fff;
  color: #000;
  padding-top: 40px;
}
.competition-category-chart .new-category-button {
  position: relative;
}
.competition-category-chart .new-category-button:before {
  content: '';
  background-image: url(../src/image/category-icon/plus-round.png);
  background-repeat: no-repeat;
  background-size: 25px 25px;
  width: 25px;
  height: 25px;
  display: inline-block;
  text-align: left;
  margin: 0 20px 0 0;
  position: absolute;
  left: 34px;
  bottom: 18px;
  cursor: pointer;
}
.competition-category-chart .btn-close {
  display: none;
  position: absolute;
  content: '';
  background-image: url(../src/image/category-icon/close.png);
  background-size: 10px 10px;
  width: 10px;
  height: 10px;
  cursor: pointer;
  right: 0;
  background-color: #fff;
  border-radius: 2px;
  background-repeat: no-repeat;
  margin: 0px 0px 0 0;
}
.competition-category-chart .btn-close:hover {
  background-color: #ddd;
}
.competition-category-chart .view-chart-type {
  margin-bottom: 20px;
}
.competition-category-chart .view-chart-type .switch {
  position: relative;
  height: 40px;
  width: 100px;
  background: rgba(0, 0, 0, 0.25);
  -webkit-box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.3), 0 1px rgba(255, 255, 255, 0.1);
  box-shadow: inset 0 1px 3px rgba(0, 0, 0, 0.3), 0 1px rgba(255, 255, 255, 0.1);
}
.competition-category-chart .view-chart-type .switch-label {
  position: relative;
  z-index: 2;
  float: left;
  width: 50px;
  line-height: 26px;
  font-size: 11px;
  color: rgba(255, 255, 255, 0.35);
  text-align: center;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.45);
  cursor: pointer;
}
.competition-category-chart .view-chart-type .switch-label:active {
  font-weight: bold;
}
.competition-category-chart .view-chart-type .switch-label-off {
  padding-left: 2px;
}
.competition-category-chart .view-chart-type .switch-label-on {
  padding-right: 2px;
}
.competition-category-chart .view-chart-type .switch-input {
  display: none;
}
.competition-category-chart .view-chart-type .switch-input:checked + .switch-label {
  color: rgba(0, 0, 0, 0.65);
  text-shadow: 0 1px rgba(255, 255, 255, 0.25);
  -webkit-transition: 0.15s ease-out;
  -moz-transition: 0.15s ease-out;
  -o-transition: 0.15s ease-out;
  transition: 0.15s ease-out;
}
.competition-category-chart .view-chart-type .switch-input:checked + .switch-label-on ~ .switch-selection {
  /* Note: left: 50% doesn't transition in WebKit */
  left: 50px;
}
.competition-category-chart .view-chart-type .switch-selection {
  display: block;
  position: absolute;
  z-index: 1;
  width: 50%;
  height: 100%;
  background: gainsboro;
  -webkit-transition: left 0.15s ease-out;
  -moz-transition: left 0.15s ease-out;
  -o-transition: left 0.15s ease-out;
  transition: left 0.15s ease-out;
}
.competition-category-chart .view-chart-type .view-by-chart-img {
  width: 32px;
  margin-top: 7px;
}
.competition-category-chart .view-chart-type .view-by-table-img {
  width: 32px;
  margin-top: 7px;
}
.competition-category-chart .chart-container {
  padding: 10px 0 10px 0;
}
.competition-category-chart .chart-container .text-title {
  padding: 0;
}
.competition-category-chart .chart-container .region .btn-region {
  height: 40px;
  border: solid 1px #8dc63f;
  font-weight: bold;
  text-transform: uppercase;
}
.competition-category-chart .chart-container .region .btn-region.first {
  border-bottom-left-radius: 20px;
  border-top-left-radius: 20px;
}
.competition-category-chart .chart-container .region .btn-region.last {
  border-bottom-right-radius: 20px;
  border-top-right-radius: 20px;
}
.competition-category-chart .chart-container .region .active {
  color: #fff;
  background-image: url("../src/image/category-icon/gradient-green.png");
  border: none;
  box-shadow: 0 0 3px 0 #8dc63f !important;
}
