.competition-category-chart #courtsize-dialog {
  position: absolute;
  display: none;
  z-index: 100;
  background-color: #FBFFF5;
  border: 1px solid #8DC63F;
  padding: 10px;
  margin-top: 10px;
  border-radius: 3px;
}
.competition-category-chart #courtsize-dialog .court-image-container {
  padding: 5px 0;
}
.competition-category-chart #courtsize-dialog p {
  font-weight: bold;
}
.competition-category-chart #courtsize-dialog .close-thik:after {
  content: 'X';
  position: absolute;
  color: #777;
  right: 5px;
  text-decoration: none;
  text-shadow: 0 1px 0 #fff;
  top: 2px;
  cursor: pointer;
}
.competition-category-chart #courtsize-dialog .btn-white {
  width: 110px;
}
.competition-category-chart #courtsize-dialog .btn-green {
  width: 110px;
}
.competition-category-chart #courtsize-dialog:before {
  content: '';
  width: 0;
  height: 0;
  border-left: 7px solid transparent;
  border-right: 7px solid transparent;
  position: absolute;
  border-top: 7px solid #000;
  text-align: center;
  top: -9px;
  left: 55px;
}
