body {
  /*font-family: 'Roboto', sans-serif !important;*/
  font-family: "arial", Helvetica Neue, Helvetica, Arial, sans-serif !important;
  font-size: 14px;
  color: #000;
  background: #f5f5f5;
}
.container {
  position: relative;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
ul,
ol {
  list-style: none;
}
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
nav,
section {
  display: block;
}
.clearfix {
  @include clearfix;
}
p {
  margin: 0;
  padding: 0;
  line-height: 1.3;
}
a {
  color: #000;
  text-decoration: none;
}
a:active,
a:visited,
a:focus,
a:hover {
  text-decoration: none;
}
a:focus,
select:focus,
input:focus,
button:focus {
  outline: none;
}
ul,
li {
  list-style-type: none;
  padding: 0;
  margin: 0;
}
/* reset webkit search input styles */
input[type=search] {
  -webkit-appearance: none;
  outline: none;
}
input[type="search"]::-webkit-search-decoration,
input[type="search"]::-webkit-search-cancel-button {
  display: none;
}
.img-responsive,
.thumbnail > img,
.thumbnail a > img,
.carousel-inner > .item > img,
.carousel-inner > .item > a > img {
  display: block;
  width: 100%;
  height: auto;
}
img {
  max-width: 100%;
  height: auto;
}
ul,
ol {
  list-style: none;
  margin: 0;
  padding: 0;
}
.row {
  margin-right: -8px;
  margin-left: -8px;
}
.container {
  padding-right: 8px;
  padding-left: 8px;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Roboto', sans-serif;
  color: #000;
  padding: 0;
  margin: 0;
  margin-bottom: 10px;
}
h1 {
  font-size: 32px;
}
h2 {
  font-size: 24px;
}
h3 {
  font-size: 18px;
}
h4 {
  font-size: 16px;
}
h5 {
  font-size: 14px;
}
h6 {
  font-size: 13px;
}
