.checkbox-custom,
.radio-custom {
  opacity: 0;
  position: absolute;
}
.checkbox-custom,
.checkbox-custom-label,
.radio-custom,
.radio-custom-label {
  display: inline-block;
  vertical-align: middle;
  /*margin: 5px;*/
  cursor: pointer;
}
.checkbox-custom-label,
.radio-custom {
  position: relative;
}
.checkbox-custom + .checkbox-custom-label:before {
  content: '';
  background: #fff;
  border: 1px solid #ddd;
  display: inline-block;
  vertical-align: middle;
  width: 17px;
  height: 17px;
  /*padding: 2px;*/
  margin-right: 10px;
  text-align: center;
  border-radius: 2px;
}
.radio-custom + .radio-custom-label:before {
  content: '';
  background: #fff;
  border: 1px solid #ddd;
  display: inline-block;
  vertical-align: middle;
  width: 17px;
  height: 17px;
  /*padding: 2px;*/
  margin-right: 10px;
  text-align: center;
  border-radius: 10px;
  line-height: 15px;
}
.checkbox-custom:checked + .checkbox-custom-label:before {
  content: "\f00c";
  font-family: 'FontAwesome';
  font-size: 13px;
  /*background: rebeccapurple;*/
  color: #8dc63f;
}
.radio-custom:checked + .radio-custom-label:before {
  content: "\f111";
  font-family: 'FontAwesome';
  font-size: 10px;
  /*background: rebeccapurple;*/
  color: #8dc63f;
  border-radius: 10px;
}
.tournapps .main .content-left .box .title input[type=checkbox]:checked + .fs-listings-input > label:after,
.tournapps .main .content-left .box .title input[type=checkbox]:checked + label:after {
  content: "\62";
  font-family: "tuor";
  display: inline-block;
  position: absolute;
  z-index: 20;
  top: 4px;
  font-size: 15px;
  font-weight: normal;
  color: #8dc63f;
}
