.c-indicator {
  position: absolute;
  top: 0;
  left: 0;
  display: block;
  width: 20px;
  height: 20px;
  font-size: 65%;
  line-height: 1rem;
  text-align: center;
  user-select: none;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 100% 100%;
  border-radius: .25rem;
}
.c-indicator-ready {
  border: 1px solid #87C137;
  color: #87C137;
}
.c-indicator-black {
  border: 1px solid #000000;
  color: #000000;
}
.c-indicator-process {
  border: 1px solid #959595;
  color: #959595;
}
.c-indicator-waiting {
  border: 1px solid #637BD4;
  color: #637BD4;
}
.c-indicator-match {
  border: 1px solid #E4B343;
  color: #E4B343;
}
.text-status > input:checked ~ .c-indicator-ready {
  color: #87C137;
  background-color: #87C137;
}
.text-status > input:active ~ .c-indicator-ready {
  color: #87C137;
}
.text-status > input:checked ~ .c-indicator-process {
  color: #959595;
  background-color: #959595;
}
.text-status > input:active ~ .c-indicator-process {
  color: #959595;
}
.text-status > input:checked ~ .c-indicator-waiting {
  color: #637BD4;
  background-color: #637BD4;
}
.text-status > input:active ~ .c-indicator-waiting {
  color: #637BD4;
}
.text-status input:checked ~ .c-indicator-match {
  color: #E4B343;
  background-color: #E4B343;
}
.text-status > input:active ~ .c-indicator-match {
  color: #E4B343;
}
.text-status input:checked ~ .c-indicator {
  background-image: url(data:image/svg+xml;base64,);
}
.text-status input:checked ~ .c-indicator-black {
  color: #000000;
  background-color: #000000;
}
.text-status > input:active ~ .c-indicator-black {
  color: #000000;
}
