/*  switch unavailable court  */
.unavailable-switch-wrapper {
    display: inline-block;
    margin-left: 15px;
}
.unavailable-switch-wrapper > input[type="checkbox"] {
    display: none;   
}
.unavailable-switch-wrapper > label {
    cursor: pointer;
    height: 0px;
    position: relative; 
    width: 40px;  
}
.unavailable-switch-wrapper > label::before {
    background: rgb(0, 0, 0);
    box-shadow: inset 0px 0px 10px rgba(0, 0, 0, 0.5);
    border-radius: 8px;
    content: '';
    height: 16px;
    margin-top: -8px;
    position:absolute;
    opacity: 0.3;
    transition: all 0.4s ease-in-out;
    width: 40px;
}
.unavailable-switch-wrapper > label::after {
    background: rgb(255, 255, 255);
    border-radius: 16px;
    box-shadow: 0px 0px 5px rgba(0, 0, 0, 0.3);
    content: '';
    height: 24px;
    left: -4px;
    margin-top: -8px;
    position: absolute;
    top: -4px;
    transition: all 0.3s ease-in-out;
    width: 24px;
}
.unavailable-switch-wrapper > input[type="checkbox"]:checked + label::before {
    background: inherit;
    opacity: 0.5;
}
.unavailable-switch-wrapper > input[type="checkbox"]:checked + label::after {
    background: inherit;
    left: 20px;
}
