.competition-stages-wrapper {
  padding-left: 50px;
  padding-bottom: 10px;
  font-family: HelveticaNeue;
  font-size: 14px;
}
.competition-stages-wrapper .col-sm-12,
.competition-stages-wrapper .col-md-12,
.competition-stages-wrapper .col-lg-12 {
  padding-left: 0;
  padding-right: 0;
}
.competition-stages-wrapper h2 {
  font-size: 20px;
  font-family: HelveticaNeue;
  font-weight: bold;
  text-align: left;
  color: #000000;
}
.competition-stages-wrapper .classifications label {
  padding-left: 0;
  margin-top: 10px;
}
.competition-stages-wrapper .classifications .col-sm-1.col-md-1.col-lg-1 {
  width: 13%;
}
.competition-stages-wrapper .categories .col-sm-1.col-md-1.col-lg-1 {
  width: 13%;
}
.competition-stages-wrapper .categories label {
  padding-left: 0;
  margin-top: 10px;
}
.competition-stages-wrapper .categories2 {
  margin-top: 10px;
}
.competition-stages-wrapper .categories2 .control-label {
  margin-right: 17px;
}
.competition-stages-wrapper .classifications-wrapper .col-sm-3.col-md-3.col-lg-3 {
  padding: 0;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-able-header {
  border-bottom: 6px solid #f7f7f7 !important;
  border: 1px solid #e9e9e9;
  padding: 10px 0px 5px 10px;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-able-header p {
  font-size: 16px;
  font-weight: bold;
  color: #4a4a4a;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-able-header p .img-male {
  margin-right: 10px;
  width: 20px;
  margin-bottom: 7px;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone {
  padding: 20px 0px 25px 25px;
  border: 1px solid #e9e9e9;
  height: 180px;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone [draggable] {
  -moz-user-select: none;
  -khtml-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .drag-box {
  width: 205px;
  height: 143px;
  border-radius: 8px;
  background-color: #fcfcfc;
  border: solid 1px rgba(224, 224, 224, 0.6);
  float: left;
  margin-right: 10px;
  position: relative;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .drag-box .box-header {
  padding: 10px 0 0 10px;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .drag-box .box-header p {
  font-size: 13px;
  font-weight: bold;
  text-align: left;
  color: #000000;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .drag-box .box-header .edit-group {
  display: none;
  float: right;
  position: absolute;
  top: 4px;
  right: 5px;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .drag-box .box-header .edit-group .img-pencil-green {
  width: 14px;
  margin-right: 5px;
  cursor: pointer;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .drag-box .box-header .edit-group .img-close-green {
  width: 13px;
  cursor: pointer;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .drag-box .box-header .edit-group .dropdown {
  position: relative;
  display: inline-block;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .drag-box .box-header .edit-group .dropdown .dropdown-content {
  display: none;
  position: absolute;
  min-width: 160px;
  z-index: 1;
  width: 180px;
  border-radius: 3px;
  background-color: #ffffff;
  box-shadow: 0 3px 3px 0 rgba(141, 198, 63, 0.5);
  border: solid 1px rgba(221, 221, 221, 0.3);
  cursor: pointer;
  z-index: 20;
  padding-left: 10px;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .drag-box .box-header .edit-group .dropdown .dropdown-content a {
  text-align: left;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  font-weight: normal;
  width: 143.6px;
  height: 27px;
  border-radius: 5px;
  position: relative;
  color: #fff;
  margin: 10px 0 10px 5px;
  padding: 4px 0 0px 0px;
  text-align: center;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .drag-box .box-header .edit-group .dropdown .prepopulated {
  background-color: #68c5d1;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .drag-box .box-header .edit-group .dropdown .populated {
  background-color: #637bd5;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .drag-box .box-header .edit-group .dropdown .in-planning {
  background-color: #ebba4a;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .drag-box .box-header .edit-group .dropdown .finished {
  background-color: #da4f6d;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .drag-box .box-header .edit-group .dropdown .playing {
  background-color: #87c137;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .drag-box .box-header .edit-group .dropdown:hover .dropdown-content {
  display: block;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .drag-box .box-header .edit-group .dropdown .selected:before {
  content: "";
  background-image: url(../src/image/stage-icon/tick-icon.png);
  background-repeat: no-repeat;
  background-size: 15px 15px;
  width: 20px;
  height: 20px;
  display: inline-block;
  text-align: left;
  margin: 0 20px 0 0;
  position: absolute;
  left: 122px;
  bottom: 0px;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .drag-box .box-content {
  padding: 0 0 0 40px;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .drag-box .box-content .img-round-robin {
  width: 125px;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .drag-box:hover {
  background-color: rgba(141, 198, 63, 0.2);
  border: solid 1px rgba(141, 198, 63, 0.6);
  cursor: move;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .drag-box:hover .edit-group {
  display: block;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .arrow-icon {
  float: left;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .arrow-icon:before {
  content: "";
  background-image: url(../src/image/stage-icon/arrow.png);
  background-repeat: no-repeat;
  background-size: 20px 40px;
  width: 40px;
  height: 40px;
  position: relative;
  top: 55px;
  left: 20px;
  display: inline-block;
  text-align: left;
  margin: 0 20px 0 0;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .add-box {
  min-width: 110px;
  height: 60px;
  float: left;
  padding: 40px 0 0 25px;
  margin-left: 30px;
  text-align: center;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .add-box .box-content .img-plus {
  width: 45px;
  cursor: pointer;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .add-box .box-content p {
  margin-top: 10px;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .copy-box {
  min-width: 110px;
  height: 60px;
  float: left;
  padding: 40px 0 0 25px;
  margin-left: 30px;
  text-align: center;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .copy-box .box-content .img-copy {
  width: 45px;
  cursor: pointer;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .copy-box .box-content p {
  margin-top: 10px;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .copy-box .dropdown {
  position: relative;
  display: inline-block;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .copy-box .dropdown .dropdown-content {
  display: none;
  position: absolute;
  min-width: 160px;
  z-index: 1;
  width: 160px;
  border-radius: 3px;
  background-color: #ffffff;
  box-shadow: 0 3px 3px 0 rgba(141, 198, 63, 0.5);
  border: solid 1px rgba(221, 221, 221, 0.3);
  left: 35px;
  bottom: 35px;
  cursor: pointer;
  z-index: 20;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .copy-box .dropdown .dropdown-content .title {
  text-align: left;
  font-weight: bold;
  display: block;
  border-bottom: 1px solid #dddddd;
  padding: 10px 0 5px 6px;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .copy-box .dropdown .dropdown-content a {
  text-align: left;
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
  font-weight: normal;
  width: 143.6px;
  height: 27px;
  border-radius: 5px;
  background-color: #87c137;
  color: #fff;
  margin: 10px 0 10px 5px;
  padding: 4px 0 0px 0px;
  text-align: center;
}
.competition-stages-wrapper .classifications-wrapper .drag-able-wrapper .drag-zone .copy-box .dropdown:hover .dropdown-content {
  display: block;
}
#newStageModal {
  font-family: HelveticaNeue;
}
#newStageModal .modal-title {
  height: 32px;
  font-family: Archive;
  font-size: 26px;
  color: #000000;
  text-shadow: 0 1px 1px rgba(0, 0, 0, 0.2);
}
#newStageModal .text-left {
  text-align: left;
}
#newStageModal label {
  font-size: 16px;
}
#newStageModal .modal-header {
  border-bottom: none;
}
#newStageModal .modal-body .type-wrapper {
  width: 174px;
  height: 143px;
  border-radius: 8px;
  border: solid 1px rgba(224, 224, 224, 0.6);
}
#newStageModal .modal-body .type-wrapper label {
  font-size: 14px;
}
#newStageModal .modal-body .type-wrapper .radio-custom-label:before {
  line-height: 14px;
}
#newStageModal .modal-body .type-wrapper .img-round-robin {
  width: 110px;
  margin-left: 45px;
}
#newStageModal .modal-body .type-wrapper .img-knockout {
  width: 110px;
  margin-left: 45px;
}
#newStageModal .modal-body .type-wrapper.left {
  margin-right: 10px;
}
#newStageModal .modal-body .type-wrapper.right {
  margin-left: 10px;
}
#newStageModal .modal-footer {
  border-top: none;
  padding: 0;
}
#newStageModal .modal-footer .btn-white {
  width: 122.5px;
  height: 32px;
}
#newStageModal .modal-footer .btn-green {
  width: 122.5px;
  height: 32px;
}
